<!--
 * @Author: linxu
 * @Date: 2021-11-16 11:37:30
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-28 16:57:00
 * @Description: 合同管理页面
-->
<template>
  <div>
    <BMainTopComponent />
    <div class="contract-manage">
      <!-- <el-button @click="">查看合同详情</el-button> -->
      <div class="search-box">
        <div class="search-item">
          <span>合同名称:</span>
          <el-input v-model="search.name"
                    placeholder="请输入关键信息以查询维保策略">
          </el-input>
        </div>
        <div class="search-item">
          <span>商务对接:</span>
          <el-select v-model="search.oppositePerson"
                     placeholder="请选择发动机品牌"
                     :popper-append-to-body="false">
            <el-option label="全部"
                       value=""></el-option>
            <el-option v-for="item in oppositePersonList"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>合同状态:</span>
          <el-select v-model="search.status"
                     placeholder="请选择维保策略状态"
                     :popper-append-to-body="false">
            <el-option label="全部"
                       value=""></el-option>
            <el-option label="生效中"
                       value="1"></el-option>
            <el-option label="即将到期"
                       value="2"></el-option>
            <el-option label="已终止"
                       value="3"></el-option>
          </el-select>
        </div>
        <el-button @click="queryTableData()">查询</el-button>
      </div>
      <div style="margin-bottom: 16px">
        <el-button @click="jump('/business/lease-manage/contract-info')">新建合同</el-button>
      </div>
      <el-table ref="mainTable"
                :data="tableData"
                style="width: 100%"
                height="100%"
                border
                tooltip-effect="light"
                v-loading="loading">
        <!-- 列表无数据 -->
        <template slot="empty">
          <el-empty image="/static/img/nodata.png"
                    :image-size="80"></el-empty>
        </template>
        <el-table-column type="index"
                         :index="indexMethod"
                         label="序号"
                         align="center"
                         width="60">
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="合同编号"
                         min-width="12%">
          <template slot-scope="scope">
            <div>{{ scope.row["DisplayName"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="Status"
                         label="签约人"
                         min-width="6%">
          <template slot-scope="scope">
            <div>{{ scope.row["Status"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="InstallationSite"
                         label="公司名称"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["InstallationSite"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="联系电话"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="商务对接"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="机组名称"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="租赁价格"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="合同状态"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="合同开始时间"
                         min-width="10%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="合同结束时间"
                         min-width="10%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="DisplayName"
                         label="续约次数"
                         min-width="8%">
          <template slot-scope="scope">
            <div>{{ scope.row["LongitudeAndLatitude"] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         width="120">
          <template slot-scope="scope">
            <div class="table-hover-show-btn">
              <el-button type="text"
                         @click="showDetail(scope.row, 0)">查看</el-button>
              <el-button type="text"
                         @click="showDetail(scope.row, 1)">续约</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <div class="pagination-box mt15">
        <el-pagination align="center"
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="pageSize"
                       :layout="layout"
                       :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { paginationMixin } from '@/mixins/paginationMixin'
export default {
  name: 'contract-manage',
  mixins: [paginationMixin],
  data () {
    return {
      oppositePersonList: [], // 商务对接人List
      search: { // 查询条件
        name: '',
        oppositePerson: '',
        status: ''
      },
      tableData: [{}]
    }
  },
  methods: {

    showDetail (row, type) {
      this.jump('/business/lease-manage/contract-info')
    },
    jump (url) {
      this.$router.push(url)
    }
  }
}
</script>

<style lang="less" scoped>
.contract-manage {
}
</style>



